@font-face {
  font-family: 'Helvetica Now Display Bold';
  src: local('HelveticaNowDisplay-Bold'), url(./HelveticaNowDisplay-Bold.ttf) format('truetype');
}

html, body {
  scroll-behavior: smooth;
}

body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Helvetica Now Display Bold', Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

code {
  font-family: 'Helvetica Now Display Bold', Times, serif, 'Courier New',
  monospace;
}