}

/* MENU STYLES */
.menu-wrap {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1;
}
  
.menu-wrap .toggler {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    cursor: pointer;
    margin: 20px 0 0 0;
    width: 50px;
    height: 50px;
    opacity: 0;
}
  
.menu-wrap .hamburger {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    width: 110px;
    height: 60px;
    padding: 1rem 1rem 0 0;
    display: flex;
    align-items: center;
    color: black;
    font-weight: 700;
    letter-spacing: 1px;
    justify-content: space-around;
    transition: ease-in 0.1s;
}
  
  /* Hamburger Line */
.menu-wrap .hamburger > div {
    position: relative;
    flex: none;
    width: 30%;
    height: 3px;
    background: rgb(0, 0, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease;
}
  
  /* Hamburger Lines - Top & Bottom */
.menu-wrap .hamburger > div::before,
.menu-wrap .hamburger > div::after {
    content: '';
    position: absolute;
    z-index: 1;
    top: -8px;
    width: 100%;
    height: 3px;
    background: inherit;
}
  
/* Moves Line Down */
.menu-wrap .hamburger > div::after {
    top: 8px;
}

.menu-wrap .toggler:hover + .hamburger {
    /* opacity: 0.8; */
    letter-spacing: 2px;
}
  
/* Toggler Animation */
.menu-wrap .toggler:checked + .hamburger > div {
    transform: rotate(135deg);
    background: #fff;
}

.menu-wrap .toggler:checked + .hamburger > p {
    color: #fff;
}
  
/* Turns Lines Into X */
.menu-wrap .toggler:checked + .hamburger > div:before,
.menu-wrap .toggler:checked + .hamburger > div:after {
    top: 0;
    transform: rotate(90deg);
}
  
/* Rotate On Hover When Checked */
.menu-wrap .toggler:checked:hover + .hamburger > div {
    transform: rotate(225deg);
}
  
/* Show Menu */
.menu-wrap .toggler:checked ~ .menu {
    visibility: visible;
}
  
.menu-wrap .toggler:checked ~ .menu > div {
    transform: scale(1);
    transition-duration: 0.75s;
}
  
.menu-wrap .toggler:checked ~ .menu > div > div {
    opacity: 1;
    transition:  opacity 0.4s ease 0.4s;
}
  
.menu-wrap .menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.menu-wrap .menu > div {
    background: rgba(15, 20, 30 , 0.97);
    /* border-radius: 50%; */
    width: 100%;
    height: 100%;
    display: flex;
    flex: none;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    transition: all 0.4s ease;
}
  
.menu-wrap .menu > div > div {
    display: flex;
    flex-direction: column;
    text-align: center;
    max-width: 90vw;
    max-height: 100vh;
    height: 100vh;
    opacity: 0;
    transition: opacity 0.4s ease;
}

.menu-wrap .menu > div > div > ul {
    height: 95%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
  
.copyrights {
    height: 5%;
    color: rgba(255, 255, 255, 0.75);
    font-size: 0.75rem;
}

.menu-wrap .menu > div > div > ul > li {
    list-style: none;
    color: #fff;
    font-size: 3.5rem;
    font-weight: 600;
    padding: 1rem;
    transition: ease-in 0.2s;
}

.menu-wrap .menu > div > div > ul > li:hover {
    opacity: 0.4;
}

.menu-wrap .menu > div > div > ul > li > a {
    color: inherit;
    text-decoration: none;
}


